import { Component, OnInit } from '@angular/core';
import {LocalStorageService} from "angular-2-local-storage";
import {Store} from "@ngrx/store";
import {go} from "@ngrx/router-store";
import { Router} from "@angular/router";

@Component({
  selector: 'app-fretron-signup',
  templateUrl: './fretron-signup.component.html',
  styleUrls: ['./fretron-signup.component.css']
})
export class FretronSignup implements OnInit {

  public name ='';
  public email ='';
  public mobileNo ='';
  public isUserExist:boolean;
  public isResend:boolean =false;
  public stopOtpTimer:any;
  public loginType:any ='';
  public min =5;
  public sec =0;
  public unSub_userInfo:any =null;


  ngOnDestroy(){
    if (typeof this.unSub_userInfo != 'undefined' && this.unSub_userInfo != null && this.unSub_userInfo != undefined)
      this.unSub_userInfo.unsubscribe();
  }

  constructor(private localStorageService: LocalStorageService,private _store:Store<any>,private _router:Router) {
    // let mob =this.localStorageService.get("USER-MOB-NO");
    // this.mobileNo =""+mob;
    // this.startTimer(300);

    this.unSub_userInfo =  this._store.select('userInfo').subscribe(value => {
      if(value==null){
        this.cancel();
        return;
      }
      this.isUserExist =value['customer']['isUserExist'];
      console.log(value);

      if(value['error'] =='' &&     this.isUserExist ==false){
        this.loginType = value['customer']['loginType'];
        if(this.loginType =='google'){
          this.email = value['customer']['email'];
          this.name = value['customer']['name'];
        }else if(this.loginType =='mobile'){
          this.mobileNo = value['customer']['mob'];
        }
      }else if(value['error'] =='' && this.isUserExist ==true){
        this.loginType = value['customer']['loginType'];
        if(this.loginType =='google'){
          this.email = value['customer']['email'];
          this.name = value['customer']['name'];
          this.mobileNo = value['customer']['mob'];
        }else if(this.loginType =='mobile'){
          this.startTimer(300);
          this.mobileNo = value['customer']['mob'];
          this.email = (value['customer']['email']== undefined)?'':(value['customer']['email']);
          this.name = value['customer']['name'];
        }
      }

      console.log(this.email);
      console.log(this.name);
      console.log(this.mobileNo);
    });
  }// close constructor

  ngOnInit(){

  }

  startTimer(timeInSec){
    this.isResend = false;
    let tempTime = timeInSec - 1;
    this.min = Math.floor(tempTime /60);
    this.sec = Math.floor(tempTime %60);
    this.stopOtpTimer =setInterval(()=>{
      tempTime = tempTime - 1;
      this.min = Math.floor(tempTime /60);
      this.sec = Math.floor(tempTime %60);
      if(tempTime ==0){
        clearInterval( this.stopOtpTimer);
        this.isResend=true;
      }
    },1000);
  }

  sendOTP(){
    if(this.fieldsValidation()){
      this.startTimer(300);
        console.log('send otp');
      this.isUserExist =true;
      console.log({otp:'956668'});
    }
  }

  fieldsValidation(){
    if(this.name==''  ||this.name==undefined){
      console.log("please fill Name");
      return false;
    }

    if(this.mobileNo==''  ||this.mobileNo==undefined){
      console.log("please fill Mobile No");
      return false;
    }

    // if(this.countryCode==''  ||this.countryCode==undefined){
    //   console.log("please fill country code");
    //   return false;
    // }
    return true;
  }

  cancel(){
    this._store.dispatch(go(['/fretronLogin']));
  }

  login(){
    let token ='eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCIsImFkbWluIjp0cnVlfQ.eyJpZCI6IjkxZDgwNzAwLTNkYzgtNDdmYS1iOTdjLTExYWE3MGRkZmY1MCIsImVtYWlsIjoic3VuaWxAZnJldHJvbi5jb20iLCJtb2JpbGVOdW1iZXIiOiI5MDg1NDMyOTUxIiwibmFtZSI6IlN1bmlsIiwiaWF0IjoxNTA5MzQ3MDI3fQ.P1-58MzIy39eR9kddIOgkvQy6JQDFP5MutB2i4eulMI';
    this.localStorageService.set("TOKEN",token);

    console.log(this._router.url);
    // this._router.navigate(['/Quetions', {id: 1234, pageid: 0}]);
    // console.log(this.localStorageService.get("TOKEN"));
    window.location.replace("http://localhost:4200/vtsdashboard");
  }

  resendOTP(){
    console.log("resend");
    this.startTimer(300);
    // this._store.dispatch({type: "OTP_GENERATION",payload:null});
    // this._loginServices.sendOTP.emit({mobileNo:this.mobNo,countryCode:this.countryCode});
    // this._store.dispatch(back());
  }
} //class close



