import {NgModule} from '@angular/core';
import {RouterModule, Routes} from '@angular/router';
import {FretronLogin} from "../components/user-login/fretron-login/fretron-login.component";
import {FretronSignup} from "../components/user-login/fretron-signup/fretron-signup.component";


const routes: Routes = [
  {path: '', redirectTo: 'fretronLogin', pathMatch: 'full'},
  {path: 'fretronLogin', component: FretronLogin},
  {path: 'fretronSignup', component: FretronSignup},
  // {path: 'dashboard', component: Dashboard},
];


@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule {
  constructor() {
  }
}



